.feedback {
  background: url("../../assets/feedback.png") no-repeat center;
  background-size: 40%;
  &__item {
    padding-right: 30px;
    &--submit {
      padding-right: 0;
      display: flex;
      justify-content: flex-end;
      > button {
        min-width: 100%;
      }
    }
  }
  .title {
    margin-bottom: 50px;
  }
}
