.main-header {
  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo {
    width: 172px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 14px;
    float: left;
    img {
      max-width: 100%;
      height: auto;
      margin-top: -5px;
    }
  }
  .ant-btn {
    margin-left: 15px;
    margin-right: 15px;
    color: white;
    font-size: 1rem;
    line-height: 1;
  }
  &__auth {
    .ant-btn:last-child {
      margin-right: 0;
      padding-right: 0;
    }
  }
  .ant-menu-dark.ant-menu-horizontal > .ant-menu-item, .ant-menu-dark.ant-menu-horizontal > .ant-menu-submenu{
    font-size: 1rem;
    color: #fff;
  }
}
